

const Navbar = () => {
    


    return (
        <nav className="nav">
            <div className="grid nav__container">
            <input type="checkbox" id="hamburger" />
            <div className="nav__logo-container">
                <img src="./images/logo.svg" alt="logo" />
                <label htmlFor="hamburger">
                <span><img src="./images/icon-hamburger.svg" alt="menu" /></span>
                </label>
            </div>  
            <div className="nav__header-container">
                <ul className="header">
                    <li className="header__list"><a href="">Home</a></li>
                    <li className="header__list"><a href="">About</a></li>
                    <li className="header__list"><a href="">Contact</a></li>
                    <li className="header__list"><a href="">Blog</a></li>
                    <li className="header__list"><a href="">Careers</a></li>
                    <li className="header__btn"><a href="#" aria-label="request invite">Request Invite</a></li> 
                </ul>
            </div>          
            
            

            </div>
        </nav>
    )
}

export default Navbar
